// *** begin request-block *** //
.request-block {
   box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
   background-color: $white;
   &__heading {
      display: flex;
      align-items: center;
      padding: 30px;
      min-height: 206px;
      background: url("../img/bg/black_bg-sm.png");
      @include cover;
      text-align: center; }
   &__title {
      @include f('int', 36);
      line-height: 1.2;
      color: $color1;
      span {
         color: $white; } }

   &__form {
      padding: 30px 41px 45px 47px;
      &-descr {
         margin-bottom: 15px;
         font-size: 18px; }
      &-block {}

      &-field {
         &:not(:last-child) {
            margin-bottom: 20px; } }
      &-btn {
         width: 100%; } } }

// end request-block //
