// *** begin section-title *** //
.section-title {
   $parent: &;
   text-align: center;
   &__text {
      position: relative;
      display: inline-flex;
      align-items: center;
      @include f('int', 36, 28);
      line-height: 1.2;
      color: $black;
      &::before, &::after {
         display: inline-flex;
         content: ''; }

      span {
         padding: 20px 22px; }

      #{$parent}_white & {
         span {
            background-color: $white; }
         &::before {
            @include sprite('sect-title_white-l'); }
         &::after {
            @include sprite('sect-title_white-r'); } }

      #{$parent}_yellow & {
         span {
            background-color: $color1; }
         &::before {
            @include sprite('sect-title_yellow-l'); }
         &::after {
            @include sprite('sect-title_yellow-r'); } } } }
// end section-title //

// *** begin section-divider *** //
.section-divider {
   @include ratio(1920, 296);
   @include cover;
   max-height: 350px;
   &_1 {
      background-image: url("../img/bg/divider_1.png"); }
   &_2 {
      background-image: url("../img/bg/divider_2.png"); } }
// end section-divider //

// *** begin video-block *** //
.video-block {
   $parent: &;
   position: relative;
   @include gpu;
   &__poster {
      @include coverdiv;
      width: 100%;
      height: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;';
      z-index: 11;
      transition: .5s ease-in-out;
      #{$parent}.is-active & {
         opacity: 0;
         pointer-events: none; } }

   video {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      object-fit: cover;
      font-family: 'object-fit: cover;'; }
   &__play-btn {
      position: absolute;
      top: calc(50% - 62px);
      left: calc(50% - 64px);
      z-index: 12;
      width: 128px;
      height: 124px;
      pointer-events: auto;
      transition: .5s ease-in-out;
      @include gpu;
      #{$parent}.is-active & {
         opacity: 0;
         transform: scale(.5);
         pointer-events: none; }
      &::after {
         @include coverdiv;
         border-radius: 100px 60px/100px 60px;
         background-color: $color2;
         transition: .4s ease-in-out;
         content: ''; }
      &:hover {
         &::after {
            border-radius: 60px 100px/60px 100px; }
         svg:nth-child(1) {
            transform: translate3d(0, 0, 0); } }
      svg {
         position: absolute;
         top: calc(50% - 20px);
         left: calc(50% - 17px);
         z-index: 1;
         font-size: 40px;
         @include gpu;
         &:nth-child(1) {
            transform: translate3d(5px, 5px, 0);
            fill: $white;
            transition: .4s ease-in-out; }
         &:nth-child(2) {
            fill: #1e0f00; } } } }

video::-webkit-media-controls-play-button,
video::-webkit-media-controls-overlay-play-button {
   display: none!important; }
// end video-block //
